export default {
	data() {
		return {
			name: '赵鸿飞',
			path: './detailed/Add_order',
			url: this.$store.state.url,
			Data: [],
			numPerPage: 20, //每条展示多少，触底后增加
			pageNum:1,

		}
	},
	mounted() {
		this.add()
		
	},
	onReachBottom: function(e) {
		console.log('触底了')
		this.pageNum += 1
		console.log(this.pageNum)
		this.add()
	},
	methods: {
		//初始化
		add() {
			uni.showLoading({title: '加载中'}); //启动过渡
			uni.request({
				url: this.url + 'PCenter/GetWorkDailiys',
				method: 'GET',
				data: {
					pageNum: this.pageNum,
					numPerPage: this.numPerPage,
					orderField: '',
					orderDirection: '',
					fullname: '',
					enginner: '',
					begin: '',
					end: '',
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {

					var asd = JSON.parse(res.data)
					if (asd.Code == 0) {
						if(this.Data[0]){
							this.Data = this.Data.concat(asd.Data.WorkDailyDtos)
							console.log(asd.Data)
						}else{
							this.Data = JSON.parse(res.data).Data.WorkDailyDtos
						}
						setTimeout(function() { uni.hideLoading();}, 10);//删除过渡
					} else {
						console.log('超时')
					}
				}
			});
		},
		//查看详情
		seedetails(i) {
			if (i.State) {
				console.log(i.WorkdailyId)
				uni.navigateTo({
					url: './detailed/details?id=' + i.WorkdailyId
				});
			}

		},
		//编辑
		edit(item) {
			console.log(item)
			this.$store.state.WorkdailyId = item.WorkdailyId
			uni.navigateTo({
				url: 'detailed/Editfile'
			});
		}
	},
	
}
